}

.footer {
    display: grid;
    grid-template-columns: 1fr;
    margin: 0 auto;
    gap: 2rem;
}

.footerLogo {
    width: auto;
    height: 6rem;
}
  
.footerIcon {
    padding:.5rem;
    color: var(--clr-grey-10);
    font-size: 2rem;
}

.footerIcon:hover {
    color: var(--clr-secondary);
}

.footer-icons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.center > h4 {
    color: var(--clr-white);
    width: 100%;
    text-align: center;
    margin-bottom: .5rem;
    font-size: 2.25rem;
}

.left-col > h4 {
    color: var(--clr-white);
    width: 100%;
    margin-bottom: .5rem;
    font-size: 2.25rem;
}

.left-col > .list-reset {
    list-style-type: none;
    padding: 0;
}

.left-col > .list-reset > li > a {
    display: block;
    font-size: 1.25rem; 
    transition: all 0.3s ease-in-out;
    text-align: center;
    text-decoration: none;
    margin: 0 0 .25rem 0;
}

.left-col > .list-reset > li > a:hover {
    text-decoration: underline;
    color: var(--clr-secondary);
    border-radius: 10px;
}

.left-col > .list-reset > li > a:hover  > .footer-contact__icon {
    color: var(--clr-secondary);
}

.right-col {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer-contact__icon {
    margin-right: .75rem;
    color: var(--clr-grey-10);
}

/**** responsive ****/
@media screen and (min-width: 1024px) {
    .footerWrapper {
        padding: 4rem 0;
    }
    .footer {
        grid-template-columns: 1fr 1fr;
    }
    .right-col {
        grid-column: 1 / -1;
    }
}

@media screen and (min-width: 1240px) {
    .footer {
        grid-template-columns: 1fr 1fr 1fr;
    }
    .right-col {
        grid-column: 3/4;
    }
}
